import React from "react";
import "./about.css";
import ME from "../../assets/me-about.jpg";
import { FaAward } from "react-icons/fa";
import { FiUsers } from "react-icons/fi";
import { VscFolderLibrary } from "react-icons/vsc";

export const About = () => {
  return (
    <section id="about">
      <h5>Get to Know</h5>
      <h2>About Me</h2>

      <div className="container about__container">
        <div className="about__me">
          <div className="about__me-image">
            <img src={ME} alt="About Image" />
          </div>
        </div>

        <div className="about__content">
          <div className="about__cards">
            <article className="about__card">
              <FaAward className="about__icon" />
              <h5>Experience </h5>
              <small>1.5+ Years Working </small>
            </article>
            {/* <article className="about__card">
              <FiUsers className="about__icon" />
              <h5>clients </h5>
              <small>200+ clients world-wide</small>
            </article> */}
            <article className="about__card">
              <VscFolderLibrary className="about__icon" />
              <h5>Projects </h5>
              <small>4+ completed </small>
            </article>
          </div>
          <p>
            Hello this is <b>Vinodpuri Goswami</b>, i'm basically a software
            test engineer but love to do Development as well. Interested in the
            role of a Software Engineer where I can utilize my skills as well as
            grow in the organization as well as learn and work on new skills and
            apply the skills that i had learned
          </p>
        </div>
      </div>
    </section>
  );
};
